/* ================================
   Ramsay - Black & White Minimal
   ================================ */

   :root {
    --black: #000000;
    --white: #FFFFFF;
    --gray-50: #FAFAFA;
    --gray-100: #F5F5F5;
    --gray-200: #EEEEEE;
    --gray-300: #E0E0E0;
    --gray-400: #BDBDBD;
    --gray-500: #9E9E9E;
    --gray-600: #757575;
    --gray-700: #616161;
    --gray-800: #424242;
    --gray-900: #212121;
    
    --ease-out: cubic-bezier(0.16, 1, 0.3, 1);
    --ease-in-out: cubic-bezier(0.4, 0, 0.2, 1);
    --spring: cubic-bezier(0.34, 1.56, 0.64, 1);
    --smooth: cubic-bezier(0.25, 0.46, 0.45, 0.94);
  }
  
  .app {
    min-height: 100vh;
    min-height: 100dvh;
    background: var(--white);
    position: relative;
    overflow-x: hidden;
  }
  
  /* ================================
     Welcome Screen
     ================================ */
  .welcome {
    position: fixed;
    inset: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 40px 24px;
    padding-top: max(40px, env(safe-area-inset-top));
    padding-bottom: max(40px, env(safe-area-inset-bottom));
    overflow: hidden;
    background: var(--white);
    animation: fadeIn 0.8s var(--ease-out);
  }
  
  .welcome-bg {
    position: absolute;
    inset: 0;
    overflow: hidden;
    pointer-events: none;
  }
  
  .blob {
    position: absolute;
    border-radius: 50%;
    background: var(--black);
    opacity: 0.03;
    filter: blur(120px);
    animation: blobFloat 25s ease-in-out infinite;
  }
  
  .blob-1 {
    width: 600px;
    height: 600px;
    top: -200px;
    right: -200px;
    animation-delay: 0s;
  }
  
  .blob-2 {
    width: 500px;
    height: 500px;
    bottom: -150px;
    left: -150px;
    animation-delay: 5s;
  }
  
  .blob-3 {
    width: 400px;
    height: 400px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    animation-delay: 10s;
  }
  
  @keyframes blobFloat {
    0%, 100% {
      transform: translate(0, 0) scale(1);
    }
    33% {
      transform: translate(50px, -80px) scale(1.2);
    }
    66% {
      transform: translate(-40px, 60px) scale(0.8);
    }
  }
  
  .noise-overlay {
    position: absolute;
    inset: 0;
    background-image: 
      repeating-linear-gradient(0deg, transparent, transparent 2px, rgba(0, 0, 0, 0.02) 2px, rgba(0, 0, 0, 0.02) 4px);
    pointer-events: none;
    opacity: 0.5;
  }
  
  .welcome-content {
    position: relative;
    z-index: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    flex: 1;
    gap: 40px;
    text-align: center;
  }
  
  .logo-container {
    position: relative;
    margin-bottom: 16px;
  }
  
  .logo {
    width: 100px;
    height: 100px;
    background: var(--black);
    border-radius: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 56px;
    font-weight: 900;
    color: var(--white);
    position: relative;
    z-index: 1;
    transform: rotate(-2deg);
    animation: logoEntrance 0.8s var(--spring) 0.2s backwards;
    box-shadow: 
      0 0 0 0 rgba(0, 0, 0, 0),
      0 0 0 0 rgba(0, 0, 0, 0);
    transition: all 0.4s var(--ease-out);
  }
  
  .logo:hover {
    transform: rotate(0deg) scale(1.05);
    box-shadow: 
      0 20px 60px rgba(0, 0, 0, 0.15),
      0 0 0 4px rgba(0, 0, 0, 0.1);
  }
  
  @keyframes logoEntrance {
    from {
      opacity: 0;
      transform: scale(0) rotate(-180deg);
    }
    60% {
      transform: scale(1.1) rotate(5deg);
    }
    to {
      transform: scale(1) rotate(-2deg);
    }
  }
  
  .logo-glow {
    position: absolute;
    inset: -30px;
    background: radial-gradient(circle, rgba(0, 0, 0, 0.1) 0%, transparent 70%);
    border-radius: 50%;
    filter: blur(30px);
    animation: glowPulse 3s ease-in-out infinite;
    z-index: 0;
  }
  
  @keyframes glowPulse {
    0%, 100% {
      transform: scale(1);
      opacity: 0.3;
    }
    50% {
      transform: scale(1.3);
      opacity: 0.5;
    }
  }
  
  .welcome-title {
    font-size: 72px;
    font-weight: 900;
    color: var(--black);
    letter-spacing: -0.05em;
    margin: 0;
    line-height: 1;
    animation: titleSlide 0.8s var(--ease-out) 0.4s backwards;
    position: relative;
  }
  
  @keyframes titleSlide {
    from {
      opacity: 0;
      transform: translateY(40px);
    }
  }
  
  .welcome-tagline {
    font-size: 18px;
    color: var(--gray-600);
    font-weight: 500;
    letter-spacing: 0.02em;
    animation: taglineFade 0.8s var(--ease-out) 0.6s backwards;
    margin: 0;
  }
  
  @keyframes taglineFade {
    from {
      opacity: 0;
      transform: translateY(20px);
    }
  }
  
  .welcome-features {
    display: flex;
    flex-wrap: wrap;
    gap: 12px;
    justify-content: center;
    margin-top: 24px;
    animation: featuresFade 0.8s var(--ease-out) 0.8s backwards;
  }
  
  @keyframes featuresFade {
    from {
      opacity: 0;
      transform: translateY(20px);
    }
  }
  
  .feature-pill {
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 14px 24px;
    background: var(--gray-50);
    border: 1.5px solid var(--gray-200);
    border-radius: 50px;
    color: var(--black);
    font-size: 14px;
    font-weight: 600;
    transition: all 0.3s var(--ease-out);
    position: relative;
    overflow: hidden;
  }
  
  .feature-pill::before {
    content: '';
    position: absolute;
    inset: 0;
    background: var(--black);
    opacity: 0;
    transition: opacity 0.3s var(--ease-out);
  }
  
  .feature-pill:hover {
    background: var(--black);
    border-color: var(--black);
    color: var(--white);
    transform: translateY(-2px);
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.12);
  }
  
  .feature-pill:hover::before {
    opacity: 1;
  }
  
  .feature-pill svg {
    width: 18px;
    height: 18px;
    stroke-width: 2.5;
    position: relative;
    z-index: 1;
    transition: transform 0.3s var(--ease-out);
  }
  
  .feature-pill:hover svg {
    transform: scale(1.1);
  }
  
  .feature-pill span {
    position: relative;
    z-index: 1;
  }
  
  .welcome-footer {
    position: relative;
    z-index: 1;
    display: flex;
    flex-direction: column;
    gap: 24px;
    animation: footerSlide 0.8s var(--ease-out) 1s backwards;
  }
  
  @keyframes footerSlide {
    from {
      opacity: 0;
      transform: translateY(30px);
    }
  }
  
  .btn-get-started {
    width: 100%;
    padding: 20px 32px;
    font-size: 17px;
    font-weight: 700;
    color: var(--white);
    background: var(--black);
    border-radius: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 12px;
    transition: all 0.4s var(--ease-out);
    position: relative;
    overflow: hidden;
    letter-spacing: 0.02em;
  }
  
  .btn-get-started::before {
    content: '';
    position: absolute;
    inset: 0;
    background: linear-gradient(135deg, rgba(255, 255, 255, 0.1) 0%, transparent 100%);
    opacity: 0;
    transition: opacity 0.3s var(--ease-out);
  }
  
  .btn-get-started::after {
    content: '';
    position: absolute;
    inset: 0;
    background: var(--white);
    transform: translateY(100%);
    transition: transform 0.4s var(--ease-out);
  }
  
  .btn-get-started:hover {
    transform: translateY(-4px);
    box-shadow: 0 12px 40px rgba(0, 0, 0, 0.2);
  }
  
  .btn-get-started:hover::before {
    opacity: 1;
  }
  
  .btn-get-started:hover::after {
    transform: translateY(0);
  }
  
  .btn-get-started:hover span,
  .btn-get-started:hover svg {
    position: relative;
    z-index: 1;
    color: var(--black);
  }
  
  .btn-get-started:active {
    transform: translateY(-2px) scale(0.98);
  }
  
  .btn-get-started span,
  .btn-get-started svg {
    position: relative;
    z-index: 1;
    transition: color 0.3s var(--ease-out);
  }
  
  .btn-get-started svg {
    width: 22px;
    height: 22px;
    transition: transform 0.4s var(--ease-out), color 0.3s var(--ease-out);
  }
  
  .btn-get-started:hover svg {
    transform: translateX(6px);
  }
  
  .terms {
    font-size: 12px;
    color: var(--gray-500);
    text-align: center;
    line-height: 1.6;
  }
  
  .terms a {
    color: var(--black);
    text-decoration: underline;
    text-underline-offset: 3px;
    transition: opacity 0.2s;
  }
  
  .terms a:hover {
    opacity: 0.7;
  }
  
  /* ================================
     Fund Screen
     ================================ */
  .fund-screen {
    position: fixed;
    inset: 0;
    display: flex;
    flex-direction: column;
    background: var(--white);
    overflow: hidden;
  }
  
  .fund-bg {
    position: absolute;
    inset: 0;
    overflow: hidden;
    pointer-events: none;
  }
  
  .gradient-orb {
    position: absolute;
    border-radius: 50%;
    background: var(--black);
    opacity: 0.02;
    filter: blur(100px);
    animation: orbFloat 20s ease-in-out infinite;
  }
  
  .orb-1 {
    width: 500px;
    height: 500px;
    top: -150px;
    right: -150px;
  }
  
  .orb-2 {
    width: 400px;
    height: 400px;
    bottom: -100px;
    left: -100px;
    animation-delay: 4s;
  }
  
  @keyframes orbFloat {
    0%, 100% {
      transform: translate(0, 0) scale(1);
    }
    50% {
      transform: translate(40px, -50px) scale(1.2);
    }
  }
  
  .back-button {
    position: absolute;
    top: max(20px, env(safe-area-inset-top));
    left: 20px;
    width: 44px;
    height: 44px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--white);
    border: 1.5px solid var(--gray-200);
    border-radius: 14px;
    color: var(--black);
    z-index: 10;
    transition: all 0.3s var(--ease-out);
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.04);
  }
  
  .back-button:hover {
    background: var(--black);
    border-color: var(--black);
    color: var(--white);
    transform: translateX(-4px);
    box-shadow: 0 4px 16px rgba(0, 0, 0, 0.12);
  }
  
  .back-button:active {
    transform: translateX(-2px) scale(0.95);
  }
  
  .back-button svg {
    width: 22px;
    height: 22px;
    transition: transform 0.3s var(--ease-out);
  }
  
  .back-button:hover svg {
    transform: translateX(-2px);
  }
  
  .fund-content {
    flex: 1;
    display: flex;
    flex-direction: column;
    padding: 80px 24px 24px;
    position: relative;
    z-index: 1;
  }
  
  .fund-header {
    text-align: center;
    margin-bottom: 56px;
    animation: fadeInUp 0.6s var(--ease-out);
  }
  
  @keyframes fadeInUp {
    from {
      opacity: 0;
      transform: translateY(20px);
    }
  }
  
  .fund-header h1 {
    font-size: 44px;
    font-weight: 900;
    color: var(--black);
    letter-spacing: -0.04em;
    margin-bottom: 12px;
    line-height: 1.1;
  }
  
  .fund-header p {
    font-size: 16px;
    color: var(--gray-600);
    font-weight: 500;
  }
  
  .amount-display {
    display: flex;
    align-items: baseline;
    justify-content: center;
    gap: 8px;
    margin-bottom: 40px;
    animation: fadeInUp 0.6s var(--ease-out) 0.1s backwards;
  }
  
  .currency-symbol {
    font-size: 64px;
    font-weight: 900;
    color: var(--gray-400);
    line-height: 1;
    transition: color 0.3s var(--ease-out);
  }
  
  .amount-input {
    font-size: 80px;
    font-weight: 900;
    color: var(--black);
    background: transparent;
    border: none;
    outline: none;
    width: auto;
    min-width: 200px;
    text-align: left;
    font-variant-numeric: tabular-nums;
    letter-spacing: -0.05em;
    transition: all 0.3s var(--ease-out);
  }
  
  .amount-input:focus + .currency-symbol,
  .amount-input:not(:placeholder-shown) + .currency-symbol {
    color: var(--black);
  }
  
  .amount-input::placeholder {
    color: var(--gray-300);
  }
  
  .preset-amounts {
    display: flex;
    justify-content: center;
    gap: 12px;
    margin-bottom: 56px;
    flex-wrap: wrap;
    animation: fadeInUp 0.6s var(--ease-out) 0.2s backwards;
  }
  
  .preset-btn {
    padding: 16px 32px;
    font-size: 16px;
    font-weight: 700;
    color: var(--gray-600);
    background: var(--white);
    border: 1.5px solid var(--gray-200);
    border-radius: 14px;
    transition: all 0.3s var(--ease-out);
    position: relative;
    overflow: hidden;
  }
  
  .preset-btn::before {
    content: '';
    position: absolute;
    inset: 0;
    background: var(--black);
    transform: scaleX(0);
    transform-origin: left;
    transition: transform 0.3s var(--ease-out);
  }
  
  .preset-btn:hover {
    border-color: var(--black);
    transform: translateY(-2px);
    box-shadow: 0 4px 16px rgba(0, 0, 0, 0.08);
  }
  
  .preset-btn:hover::before {
    transform: scaleX(1);
  }
  
  .preset-btn:hover span {
    position: relative;
    z-index: 1;
    color: var(--white);
  }
  
  .preset-btn.active {
    background: var(--black);
    border-color: var(--black);
    color: var(--white);
    box-shadow: 0 6px 24px rgba(0, 0, 0, 0.15);
    transform: translateY(-2px);
  }
  
  .preset-btn.active::before {
    transform: scaleX(1);
  }
  
  .earning-preview {
    background: var(--gray-50);
    border: 1.5px solid var(--gray-200);
    border-radius: 20px;
    padding: 28px;
    display: flex;
    gap: 28px;
    justify-content: space-around;
    margin-bottom: 32px;
    animation: fadeInUp 0.6s var(--ease-out) 0.3s backwards;
    transition: all 0.3s var(--ease-out);
  }
  
  .earning-preview:hover {
    border-color: var(--black);
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.06);
  }
  
  .earning-item {
    display: flex;
    flex-direction: column;
    gap: 8px;
    flex: 1;
  }
  
  .earning-label {
    font-size: 12px;
    font-weight: 600;
    color: var(--gray-500);
    text-transform: uppercase;
    letter-spacing: 0.08em;
  }
  
  .earning-value {
    font-size: 22px;
    font-weight: 900;
    color: var(--black);
    font-variant-numeric: tabular-nums;
    letter-spacing: -0.02em;
  }
  
  .earning-divider {
    width: 1px;
    background: var(--gray-200);
    margin: 0 8px;
  }
  
  .fund-footer {
    padding: 24px;
    padding-bottom: max(24px, env(safe-area-inset-bottom));
    display: flex;
    flex-direction: column;
    gap: 20px;
    position: relative;
    z-index: 1;
  }
  
  .apple-pay-button {
    width: 100%;
    height: 60px;
    background: var(--black);
    border-radius: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 12px;
    color: var(--white);
    font-size: 17px;
    font-weight: 600;
    transition: all 0.4s var(--ease-out);
    position: relative;
    overflow: hidden;
    letter-spacing: 0.02em;
  }
  
  .apple-pay-button::before {
    content: '';
    position: absolute;
    inset: 0;
    background: var(--white);
    transform: translateY(100%);
    transition: transform 0.4s var(--ease-out);
  }
  
  .apple-pay-button:hover {
    transform: translateY(-4px);
    box-shadow: 0 12px 40px rgba(0, 0, 0, 0.25);
  }
  
  .apple-pay-button:hover::before {
    transform: translateY(0);
  }
  
  .apple-pay-button:hover .apple-logo,
  .apple-pay-button:hover span {
    position: relative;
    z-index: 1;
    color: var(--black);
  }
  
  .apple-pay-button:active {
    transform: translateY(-2px) scale(0.98);
  }
  
  .apple-pay-button:disabled {
    opacity: 0.4;
    cursor: not-allowed;
    transform: none;
  }
  
  .apple-logo {
    width: 22px;
    height: 22px;
    position: relative;
    z-index: 1;
    transition: color 0.3s var(--ease-out);
  }
  
  .apple-pay-button span {
    position: relative;
    z-index: 1;
    transition: color 0.3s var(--ease-out);
  }
  
  .alt-payment {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 16px;
    font-size: 14px;
  }
  
  .link-btn {
    font-size: 15px;
    font-weight: 600;
    color: var(--black);
    background: none;
    border: none;
    padding: 8px;
    transition: all 0.2s var(--ease-out);
    position: relative;
  }
  
  .link-btn::after {
    content: '';
    position: absolute;
    bottom: 4px;
    left: 8px;
    right: 8px;
    height: 2px;
    background: var(--black);
    transform: scaleX(0);
    transition: transform 0.3s var(--ease-out);
  }
  
  .link-btn:hover::after {
    transform: scaleX(1);
  }
  
  .divider {
    color: var(--gray-400);
    font-size: 13px;
  }
  
  .skip-btn {
    font-size: 15px;
    font-weight: 600;
    color: var(--gray-500);
    background: none;
    border: none;
    padding: 12px;
    text-align: center;
    transition: color 0.2s var(--ease-out);
  }
  
  .skip-btn:hover {
    color: var(--black);
  }
  
  /* ================================
     Processing Screen
     ================================ */
  .processing-screen {
    position: fixed;
    inset: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--white);
    overflow: hidden;
  }
  
  .processing-bg {
    position: absolute;
    inset: 0;
    overflow: hidden;
  }
  
  .processing-wave {
    position: absolute;
    width: 200%;
    height: 200%;
    background: radial-gradient(circle, rgba(0, 0, 0, 0.03) 0%, transparent 70%);
    border-radius: 50%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    animation: wavePulse 4s ease-in-out infinite;
  }
  
  .wave-2 {
    animation-delay: 1.3s;
    background: radial-gradient(circle, rgba(0, 0, 0, 0.02) 0%, transparent 70%);
  }
  
  .wave-3 {
    animation-delay: 2.6s;
    background: radial-gradient(circle, rgba(0, 0, 0, 0.015) 0%, transparent 70%);
  }
  
  @keyframes wavePulse {
    0%, 100% {
      transform: translate(-50%, -50%) scale(0.8);
      opacity: 0.5;
    }
    50% {
      transform: translate(-50%, -50%) scale(1.4);
      opacity: 0.8;
    }
  }
  
  .processing-content {
    position: relative;
    z-index: 1;
    text-align: center;
    color: var(--black);
  }
  
  .processing-spinner {
    width: 100px;
    height: 100px;
    margin: 0 auto 40px;
    position: relative;
  }
  
  .spinner-track {
    position: absolute;
    inset: 0;
    border: 3px solid var(--gray-200);
    border-radius: 50%;
  }
  
  .spinner-fill {
    position: absolute;
    inset: 0;
    border: 3px solid transparent;
    border-top-color: var(--black);
    border-right-color: var(--black);
    border-radius: 50%;
    animation: spin 1.2s linear infinite;
  }
  
  @keyframes spin {
    to {
      transform: rotate(360deg);
    }
  }
  
  .spinner-icon {
    position: absolute;
    inset: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--gray-50);
    border: 1.5px solid var(--gray-200);
    border-radius: 50%;
  }
  
  .spinner-icon svg {
    width: 36px;
    height: 36px;
    color: var(--black);
    animation: iconPulse 2s ease-in-out infinite;
  }
  
  @keyframes iconPulse {
    0%, 100% {
      opacity: 1;
      transform: scale(1);
    }
    50% {
      opacity: 0.6;
      transform: scale(0.95);
    }
  }
  
  .processing-content h2 {
    font-size: 32px;
    font-weight: 900;
    margin-bottom: 12px;
    color: var(--black);
    letter-spacing: -0.03em;
  }
  
  .processing-content p {
    font-size: 16px;
    color: var(--gray-600);
    font-weight: 500;
  }
  
  /* ================================
     Success Screen
     ================================ */
  .success-screen {
    position: fixed;
    inset: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--white);
    overflow: hidden;
    animation: zoomToDashboard 2.2s var(--ease-out) forwards;
  }
  
  @keyframes zoomToDashboard {
    0% {
      opacity: 1;
      transform: scale(1);
    }
    60% {
      opacity: 1;
      transform: scale(1);
    }
    80% {
      opacity: 0.8;
      transform: scale(1.2);
    }
    100% {
      opacity: 0;
      transform: scale(15);
    }
  }
  
  .success-confetti {
    position: absolute;
    inset: 0;
    pointer-events: none;
  }
  
  .confetti-piece {
    position: absolute;
    width: 8px;
    height: 8px;
    background: var(--black);
    top: -10px;
    left: var(--x, 50%);
    border-radius: 2px;
    animation: confettiFall 2.5s ease-out var(--delay, 0s) forwards;
    transform: rotate(var(--rotation, 0deg));
    opacity: 0.8;
  }
  
  @keyframes confettiFall {
    to {
      top: 110%;
      transform: rotate(calc(var(--rotation, 0deg) + 1080deg)) translateX(calc(var(--x, 50%) * 0.1));
      opacity: 0;
    }
  }
  
  .success-content {
    position: relative;
    z-index: 1;
    text-align: center;
    color: var(--black);
    animation: successPop 0.6s var(--spring), zoomContent 2.2s var(--ease-out) forwards;
  }
  
  @keyframes zoomContent {
    0% {
      transform: scale(1);
      opacity: 1;
    }
    60% {
      transform: scale(1);
      opacity: 1;
    }
    80% {
      transform: scale(0.8);
      opacity: 0.9;
    }
    100% {
      transform: scale(0.1);
      opacity: 0;
    }
  }
  
  @keyframes successPop {
    from {
      opacity: 0;
      transform: scale(0.7);
    }
  }
  
  .success-check {
    width: 100px;
    height: 100px;
    margin: 0 auto 32px;
    background: var(--black);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 20px 60px rgba(0, 0, 0, 0.15);
    animation: checkPop 0.6s var(--spring), zoomCheck 2.2s var(--ease-out) forwards;
    position: relative;
    transform-origin: center center;
  }
  
  @keyframes zoomCheck {
    0% {
      transform: scale(1);
    }
    60% {
      transform: scale(1);
    }
    80% {
      transform: scale(1.3);
    }
    100% {
      transform: scale(20);
    }
  }
  
  .success-check::before {
    content: '';
    position: absolute;
    inset: -10px;
    border: 2px solid var(--black);
    border-radius: 50%;
    opacity: 0;
    animation: ripple 1s ease-out 0.3s;
  }
  
  @keyframes checkPop {
    0% {
      transform: scale(0) rotate(-180deg);
    }
    60% {
      transform: scale(1.15) rotate(10deg);
    }
    100% {
      transform: scale(1) rotate(0deg);
    }
  }
  
  @keyframes ripple {
    0% {
      transform: scale(0.8);
      opacity: 1;
    }
    100% {
      transform: scale(1.5);
      opacity: 0;
    }
  }
  
  .success-check svg {
    width: 50px;
    height: 50px;
