  }
  
  .savings-btn.primary {
    background: var(--black);
    border-color: var(--black);
    color: var(--white);
    box-shadow: 0 4px 16px rgba(0, 0, 0, 0.12);
  }
  
  .savings-btn.primary::before {
    background: var(--white);
    transform: scaleX(0);
  }
  
  .savings-btn.primary:hover {
    box-shadow: 0 6px 24px rgba(0, 0, 0, 0.18);
  }
  
  .savings-btn.primary:hover::before {
    transform: scaleX(1);
  }
  
  .savings-btn.primary:hover span,
  .savings-btn.primary:hover svg {
    color: var(--black);
  }
  
  .savings-btn svg {
    width: 18px;
    height: 18px;
  }
  
  .total-deposits {
    display: flex;
    align-items: center;
    gap: 12px;
    padding: 16px;
    background: var(--gray-50);
    border-radius: 14px;
    font-size: 12px;
    font-weight: 600;
    color: var(--gray-600);
    text-transform: uppercase;
    letter-spacing: 0.05em;
  }
  
  .total-deposits svg {
    width: 18px;
    height: 18px;
    color: var(--black);
  }
  
  .how-it-works {
    margin-bottom: 40px;
    animation: fadeInUp 0.6s var(--ease-out) 0.3s backwards;
  }
  
  .how-it-works h3 {
    font-size: 22px;
    font-weight: 900;
    color: var(--black);
    margin-bottom: 24px;
    letter-spacing: -0.02em;
  }
  
  .steps {
    display: flex;
    flex-direction: column;
    gap: 16px;
  }
  
  .step {
    display: flex;
    align-items: flex-start;
    gap: 16px;
    padding: 20px;
    background: var(--white);
    border: 1.5px solid var(--gray-200);
    border-radius: 18px;
    transition: all 0.3s var(--ease-out);
  }
  
  .step:hover {
    border-color: var(--black);
    transform: translateX(4px);
    box-shadow: 0 4px 16px rgba(0, 0, 0, 0.06);
  }
  
  .step-number {
    width: 40px;
    height: 40px;
    background: var(--black);
    border-radius: 12px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 18px;
    font-weight: 900;
    color: var(--white);
    flex-shrink: 0;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    transition: all 0.3s var(--ease-out);
  }
  
  .step:hover .step-number {
    transform: scale(1.1) rotate(5deg);
  }
  
  .step-content {
    flex: 1;
  }
  
  .step-content h4 {
    font-size: 18px;
    font-weight: 800;
    color: var(--black);
    margin-bottom: 6px;
    letter-spacing: -0.01em;
  }
  
  .step-content p {
    font-size: 14px;
    color: var(--gray-600);
    line-height: 1.6;
